<template>
  <div class="column">
    <table class="table is-fullwidth">
      <thead>
        <tr>
          <th>Username</th>
          <th>Email</th>
          <th>Department</th>
          <th>Role</th>
          <th>Status</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(user, key) in users" :key="key">
          <td class="is-capitalized">
            {{ user.first_name + " " + user.last_name }}
          </td>
          <td>{{ user.email }}</td>
          <td>{{ user.department }}</td>
          <td>{{ user.role }}</td>
          <td>
            <span :class="{ 'has-text-danger': !isConfirmed(user) }">{{
              format(user.is_confirmed)
            }}</span>
          </td>
          <td>
            <button
              class="button is-link"
              @click="confirm(user)"
              :disabled="isConfirmed(user)"
            >
              Confirm
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  name: "UsersListingComponent",
  created() {
    this.$store.dispatch(
      "fetchStaff",
      this.$store.state.authentication.inMemoryToken
    );
  },
  methods: {
    confirm(user) {
      user.is_confirmed = true;
      this.$socket.emit("update-user", user);
    },
    format(status) {
      if (status) return "Confirmed";
      return "Not confirmed";
    },
    isConfirmed(user) {
      return user.is_confirmed === true;
    }
  },
  computed: {
    users() {
      return this.$store.state.admin.users;
    }
  }
};
</script>
